import React, { useContext } from 'react';
import { Link } from 'gatsby';
import AccountNavStyle from '../../styles/nav/AccountNavStyle';
import Burguer from './Burguer';
import cart from '../../assets/images/cart.svg';
import AuthContext from '../../context/auth/authContext'

const AccountNav = ({ open, setOpen }) => {
  const authContext = useContext(AuthContext);
  const { isAuthenticated, logout, username } = authContext;

  const handleLogout = () => {
    logout();
  }
  
  return (
    <AccountNavStyle>
      <li>
        {isAuthenticated ? (
          <ul>
            <li>Hola, {username}</li>
            <li><Link to='/app/profile'>Account</Link></li>
            <li><a onClick={handleLogout} href='#'>Logout</a></li>
          </ul>

        ) : (
          <ul>
            <li>
              <Link to='/app/login' onClick={() => setOpen(!open)}>
                Login
        </Link>
            </li>
            <li>
              <Link to='/app/register' onClick={() => setOpen(!open)}>
                Register
        </Link>
            </li>
          </ul>
        )}
      </li>
      <li className='cart-container'>
        <img className='icon-cart' src={cart} alt='cart' />
        <span className='count-cart'>0</span>
      </li>
      <Burguer open={open} setOpen={setOpen} />
    </AccountNavStyle>
  )
};

export default AccountNav;
