import { createGlobalStyle } from 'styled-components';
import lightFont from '../assets/fonts/bryantlight.woff';
import mediumFont from '../assets/fonts/bryantmedium.woff';
import regularFont from '../assets/fonts/bryantregular.woff';

const Typography = createGlobalStyle`

    @font-face {
        font-family: lightFont;
        src: url(${lightFont});
    }
    @font-face {
        font-family: mediumFont;
        src: url(${mediumFont});
    }
    @font-face {
        font-family: regularFont;
        src: url(${regularFont});
    }

    html {
        font-family: regularFont;
    }
    
`;

export default Typography;
