import React from 'react';
import { Link } from 'gatsby';
import LinkListStyle from '../../styles/nav/LinkListStyle';

const LinkList = ({ open, setOpen }) => {
  return (
    <LinkListStyle open={open} setOpen={setOpen}>
      <li>
        <Link to='/productos' onClick={() => setOpen(!open)}>
          Productos
        </Link>
      </li>
      <li>
        <Link to='/recetas' onClick={() => setOpen(!open)}>
          Recetas
        </Link>
      </li>
      <li>
        <Link to='/nosotros' onClick={() => setOpen(!open)}>
          Nosotros
        </Link>
      </li>

    </LinkListStyle>
  )
};


export default LinkList;